@font-face {
    font-family: Playfair;
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

@font-face {
    font-family: Roboto;
    src: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
}


p {
    color: #8a8a8a;
    font-family: Roboto;
}

h1,
h2,
h3 {
    font-family: Playfair;
}

.logo {
    width: 20px;
    height: auto;
}

header {
    margin-bottom: 60px;

    .header-portrait {
        margin: 25px;
        position: relative;
        height: 500px;
        width: auto;
        background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
        background-position: center;
        background-size: cover;

        h2 {
            position: absolute;
            bottom: 0;
            left: 15px;
            color: white;
            font-size: 4rem;
            font-weight: 100;
        }
    }

    .about {

        padding: 10px 20px 20px;

        h1 {
            font-size: 3rem;
            text-align: center;
            width: 100%;
            margin: 15px auto;
        }

        p {
            margin: 20px auto;
            font-size: 1.4rem;
        }

        .icons {
            display: flex;
            justify-content: center;
            align-items: center;
            gap: 10px;
            width: 100%;

            .logo {
                width: 35px;
                height: auto;
            }
        }
    }

}

body:before {
    z-index: -1;
    position: absolute;
    background: #0891b2;
    content: "";
    min-height: 350px;
    width: 100%;
    transform: skewY(-25deg) scaleY(3) scaleX(1);
    transform-origin: bottom center;
    box-shadow: 0px 0px 8px 4px rgba(67, 67, 67, 0.5);
}

main {
    margin-bottom: 60px;
    padding: 20px;

    h1 {
        font-size: 2.5rem;
        text-align: center;
        width: 100%;
        margin: 10px auto;
    }

    .card-layout {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
        grid-column-gap: 20px;
        grid-row-gap: 60px;
        margin-bottom: 40px;

        .card {
            box-shadow: 3px 3px 6px 3px rgba(152, 152, 152, 0.5);

            >div:first-child {
                display: flex;
                align-items: center;
                justify-content: center;
                height: 250px;
                color: white;
                font-family: Playfair;
                font-size: 2.5rem;
                text-align: center;
            }

            .card-info {
                >div {
                    display: flex;
                    width: 100%;
                    justify-content: space-between;
                    align-items: end;
                    padding: 25px;

                    h3 {
                        font-size: 1.5rem;
                    }
                }

                p {
                    text-align: center;
                    min-height: 120px;
                }
            }
        }
    }
}

footer {
    background-color: #0891b2;

    .contact {
        padding: 20px 15px 50px;
        color: white;

        h1 {
            font-size: 2.5rem;
            text-align: center;
            width: 100%;
            padding: 25px auto;
        }

        p {
            margin: 15px 0px;
            color: white;

            span img {
                display: none;
            }
        }

        .icons {
            display: flex;
            justify-content: center;
            align-items: center;
            gap: 10px;
            width: 100%;
            margin: 20px auto;

            .logo {
                width: 35px;
                height: auto;
            }
        }



    }


    .footer-portrait {
        height: 600px;
        background-image: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
        background-position: center;
        background-size: cover;
    }

}


* {
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    font-size: 16px;
}

@media screen and (min-width: 500px) {

    body:before {
        z-index: -1;
        position: absolute;
        background: #0891b2;
        content: "";
