        width: 100%;
        transform: skewY(-10deg) translateY(-45%) scaleY(3) scaleX(1);
        transform-origin: bottom center;
        box-shadow: 0px 0px 8px 4px rgba(67, 67, 67, 0.5);
    }

    header {
        margin: 120px 12vw 20px;
        background-color: white;
        box-shadow: 3px 3px 6px 3px rgba(152, 152, 152, 0.5);


        .header-portrait {
            position: static;
            float: left;
            margin-top: -100px;
            margin-left: -80px;
            top: 10px;
            height: 400px;
            width: 300px;
            background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
            background-position: center;
            background-size: cover;


            h2 {
                position: absolute;
                top: 30px;
                left: 38%;
                color: white;
                font-size: clamp(2.5rem, 7vw, 3.5rem);
                width: 60%;
                font-weight: 100;
            }
        }

        .about {

            h1 {
                font-size: 2.5rem;
                text-align: center;
                width: 100%;
                margin: 15px auto;
            }

            .icons {
                display: none;
            }
        }

    }

    main {
        margin: 120px 12vw 20px;
        padding-top: 10px;
        padding: 10px;

        .about>h1 {
            padding-top: 20px;
            text-align: center;
        }
    }

    footer {
        background-color: #0891b2;
        display: flex;
        padding: 40px 30px 80px;


        .contact {
            width: 50%;
            padding: 20px 15px 50px;
            color: white;

            h1 {
                text-align: left;
                font-size: 2.5rem;
                width: 100%;
                padding: 25px 0px 0px;
            }

            p {
                margin: 35px 0px;
                color: white;

                span {
                    display: flex;
                    align-items: center;
                    color: white;

                    img {
                        margin-right: 10px;
                        display: initial;
                    }

                }
            }

            .icons {
                display: flex;
                justify-content: start;
                align-items: center;
                gap: 10px;
                width: 100%;
                margin: 20px auto;

                .logo {
                    width: 35px;
                    height: auto;
                }
            }



        }


        .footer-portrait {
            height: 500px;
            width: 50%;
            background-image: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
            background-position: center;
            background-size: cover;
        }

    }
}

@media screen and (min-width: 1000px) {

