            width: 400px;
            background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
            background-position: center;
            background-size: cover;


            h2 {
                position: absolute;
                top: auto;
                bottom: 0px;
                left: 0;
                color: white;
                font-size: 2.5rem;
                width: 100%;
                font-weight: 100;
                text-align: center;
            }
        }

        .about {
            position: relative;
            background-color: white;
            box-shadow: 3px 3px 6px 3px rgba(152, 152, 152, 0.5);
            width: 65%;
            height: 400px;

            h1 {
                font-size: 2.5rem;
                text-align: left;
                width: 100%;
                margin: 15px auto;
            }

            p {
                margin: 10px auto;
                font-size: 1.4rem;
                height: 245px;
            }

            .icons {
                display: flex;
                justify-content: end;
                width: 100%;
            }
        }

    }

    main {
        margin: 90px 6vw 20px;
        padding-top: 10px;
        padding: 10px;

        .about>h1 {
            padding-top: 20px;
            text-align: center;
        }
    }

